.main {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1200px;
    height: 680px;

    .form {
        display: flex;
        flex-direction: column;

        &__select {
            width: 320px;
            height: 48px;
        }
    }

    .map {
        background-color: rgb(221, 221, 221);
        width: 100%;
    }
}
